import React, { useState, useEffect } from "react";
import axios from "./axios";
import "./Row.css";
import YouTube from "react-youtube";
import movieTrailer from "movie-trailer";

const base_url = "https://image.tmdb.org/t/p/original/";

function Row({ title, fetchUrl, isLargeRow }) {
  const [movies, setMovies] = useState([]);
  const [trailerUrl, setTrailerUrl] = useState("");

  //will run based on a stated condition//
  useEffect(() => {
    async function fetchData() {
      const request = await axios.get(fetchUrl); // fetch data from the Url provided
      console.table(request.data.results); // this line represents the data recived from the TMDB api (data.results are properties of axios)
      setMovies(request.data.results); // the value of movies has been replaced/updated with data received from the Url
      return request;
    }
    fetchData();
  }, [fetchUrl]); //state the variable as the dependecy of useeffect

  console.log(movies); //movies is now request.data.results movies have been updated with data(arrays) that has been fetched from the url

  const opts = {
    height: "390",
    width: "100%",
    playerVars: {
      autoplay: 1,
    },
  };

  const handleClick = (movie) => {
    if (trailerUrl) {
      setTrailerUrl("");
    } else {
      movieTrailer(movie?.name || "")
        .then((url) => {
          const urlParams = new URLSearchParams(new URL(url).search);
          setTrailerUrl(urlParams.get("v"));
        })
        .catch((error) => console.log(error));
    }
  };

  return (
    <div className="row">
      <h2>{title}</h2>

      <div className="row__posters">
        {movies.map((movie) => (
          <img
            onClick={() => handleClick(movie)}
            key={movie.id}
            className={`row__poster ${isLargeRow && "row__posterLarge "}`}
            src={`${base_url}${
              isLargeRow ? movie.poster_path : movie.backdrop_path
            }`} //movie.poster__path and .backdrop_path is the results recived from the request made with axios
            alt={movie.name}
          />
        ))}
      </div>
      {trailerUrl && <YouTube videoId={trailerUrl} opts={opts} />}
    </div>
  );
}

export default Row;
